
.achievement-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.achievement-panel {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  border: 2px solid rgba(255, 215, 0, 0.4);
  border-radius: 16px;
  width: 95%;
  max-width: 600px;
  max-height: 85vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 
    0 10px 40px rgba(0, 0, 0, 0.6),
    0 0 30px rgba(255, 215, 0, 0.1);
}

// 面板头部
.panel-header {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  background: linear-gradient(90deg, rgba(255, 215, 0, 0.15), transparent);
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
  gap: 12px;

  .header-title {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 18px;
    font-weight: bold;
    color: #ffd700;

    i {
      font-size: 20px;
      animation: trophy-pulse 2s ease-in-out infinite;
    }
  }

  .header-stats {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-left: auto;
    margin-right: 12px;

    .stat-item {
      display: flex;
      align-items: center;
      gap: 5px;
      font-size: 12px;
      color: #a0b8d0;
      padding: 4px 10px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 12px;

      i {
        font-size: 11px;
        color: #ffd700;
      }
    }

    .title-badge {
      background: rgba(255, 215, 0, 0.15);
      border: 1px solid rgba(255, 215, 0, 0.3);
      color: #ffd700;
    }
  }

  .close-btn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    color: #a0b8d0;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background: rgba(255, 100, 100, 0.2);
      border-color: rgba(255, 100, 100, 0.4);
      color: #ff6464;
    }
  }
}

@keyframes trophy-pulse {
  0%, 100% {
    transform: scale(1);
    filter: drop-shadow(0 0 3px #ffd700);
  }
  50% {
    transform: scale(1.1);
    filter: drop-shadow(0 0 8px #ffd700);
  }
}

// 筛选标签容器
.filter-tabs-wrapper {
  position: relative;
  background: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

// 筛选标签
.filter-tabs {
  display: flex;
  padding: 10px 16px;
  gap: 6px;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  scroll-behavior: smooth;

  &::-webkit-scrollbar {
    display: none;
  }

  .filter-btn {
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 6px 12px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid transparent;
    border-radius: 16px;
    color: #8090a0;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
    white-space: nowrap;
    flex-shrink: 0;

    .filter-icon {
      font-size: 12px;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.1);
      color: #a0b8d0;
    }

    &.active {
      background: rgba(255, 215, 0, 0.15);
      border-color: rgba(255, 215, 0, 0.4);
      color: #ffd700;
    }
  }
}

// 滑动指示器
.scroll-indicator {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 2;
  animation: indicator-pulse 2s ease-in-out infinite;
  
  i {
    font-size: 12px;
    color: #ffd700;
    filter: drop-shadow(0 0 3px rgba(255, 215, 0, 0.6));
  }
  
  &.left {
    left: 0;
    background: linear-gradient(to right, rgba(0, 0, 0, 0.4) 0%, transparent 100%);
  }
  
  &.right {
    right: 0;
    background: linear-gradient(to left, rgba(0, 0, 0, 0.4) 0%, transparent 100%);
  }
}

@keyframes indicator-pulse {
  0%, 100% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
}

// 成就容器
.achievements-container {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  min-height: 200px;
  max-height: 300px;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 215, 0, 0.3);
    border-radius: 3px;

    &:hover {
      background: rgba(255, 215, 0, 0.5);
    }
  }
}

// 成就网格
.achievements-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

// 成就卡片
.achievement-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  padding: 14px;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.06);
    transform: translateX(4px);
  }

  // 稀有度样式
  &.rarity-普通 {
    border-left: 3px solid #a0a0a0;
  }
  &.rarity-稀有 {
    border-left: 3px solid #4a9eff;
  }
  &.rarity-史诗 {
    border-left: 3px solid #a855f7;
  }
  &.rarity-传说 {
    border-left: 3px solid #ff8c00;
  }
  &.rarity-神话 {
    border-left: 3px solid #ff4444;
    background: rgba(255, 68, 68, 0.05);
  }

  .card-header {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 8px;

    .rarity-icon {
      font-size: 14px;
    }

    .achievement-name {
      font-size: 14px;
      font-weight: 600;
      color: #e0e8f0;
      flex: 1;
    }

    .type-icon {
      font-size: 14px;
      opacity: 0.7;
    }
  }

  .card-body {
    .achievement-desc {
      font-size: 12px;
      color: #8090a0;
      line-height: 1.5;
      margin: 0 0 6px 0;
    }

    .first-impression {
      font-size: 11px;
      color: #a0b8d0;
      font-style: italic;
      margin: 0;
      padding: 6px 10px;
      background: rgba(255, 255, 255, 0.03);
      border-radius: 6px;
      border-left: 2px solid rgba(255, 215, 0, 0.3);
    }
  }

  .card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    padding-top: 8px;
    border-top: 1px solid rgba(255, 255, 255, 0.05);

    .unlock-time {
      font-size: 10px;
      color: #607080;
      display: flex;
      align-items: center;
      gap: 4px;

      i {
        font-size: 9px;
      }
    }

    .rarity-label {
      font-size: 10px;
      padding: 2px 8px;
      border-radius: 8px;
      background: rgba(255, 255, 255, 0.05);
      color: #8090a0;
    }
  }
}

// 空状态
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  color: #506070;

  i {
    font-size: 48px;
    margin-bottom: 16px;
    opacity: 0.3;
  }

  p {
    margin: 0;
    font-size: 14px;
  }

  .empty-hint {
    font-size: 12px;
    margin-top: 8px;
    opacity: 0.7;
  }
}

// 里程碑区域
.milestones-section {
  padding: 16px;
  background: rgba(0, 0, 0, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.05);

  .section-title {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    font-weight: 600;
    color: #a0b8d0;
    margin-bottom: 12px;

    i {
      color: #ffd700;
    }
  }
}

.milestones-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;

  .milestone-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-radius: 10px;

    .milestone-icon {
      font-size: 18px;
    }

    .milestone-info {
      .milestone-value {
        font-size: 16px;
        font-weight: bold;
        color: #e0e8f0;
      }

      .milestone-label {
        font-size: 10px;
        color: #607080;
      }
    }
  }
}

// 动画
.panel-fade-enter-active,
.panel-fade-leave-active {
  transition: all 0.3s ease;
}

.panel-fade-enter-from,
.panel-fade-leave-to {
  opacity: 0;

  .achievement-panel {
    transform: scale(0.9) translateY(20px);
  }
}

// ==================== 手机端响应式适配 ====================
@media screen and (max-width: 480px) {
  .achievement-panel {
    width: 100%;
    max-width: none;
    max-height: 90vh;
    border-radius: 16px 16px 0 0;
    margin-top: auto;
  }

  .panel-header {
    padding: 14px 16px;
    flex-wrap: wrap;

    .header-title {
      font-size: 16px;

      i {
        font-size: 18px;
      }
    }

    .header-stats {
      order: 3;
      width: 100%;
      margin-top: 10px;
      margin-left: 0;
      justify-content: flex-start;

      .stat-item {
        font-size: 11px;
        padding: 3px 8px;
      }
    }
  }

  .filter-tabs {
    padding: 8px 12px;
    gap: 4px;

    .filter-btn {
      padding: 5px 10px;
      font-size: 11px;

      .filter-label {
        display: none;
      }

      .filter-icon {
        font-size: 14px;
      }
    }
  }
  
  .scroll-indicator {
    width: 25px;
    
    i {
      font-size: 11px;
    }
  }

  .achievements-container {
    padding: 12px;
    max-height: 250px;
  }

  .achievement-card {
    padding: 12px;

    .card-header {
      .achievement-name {
        font-size: 13px;
      }
    }

    .card-body {
      .achievement-desc {
        font-size: 11px;
      }

      .first-impression {
        font-size: 10px;
        padding: 5px 8px;
      }
    }
  }

  .milestones-section {
    padding: 12px;
  }

  .milestones-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;

    .milestone-item {
      padding: 8px;
      gap: 6px;

      .milestone-icon {
        font-size: 16px;
      }

      .milestone-info {
        .milestone-value {
          font-size: 14px;
        }

        .milestone-label {
          font-size: 9px;
        }
      }
    }
  }

  .empty-state {
    padding: 30px 16px;

    i {
      font-size: 36px;
    }

    p {
      font-size: 13px;
    }

    .empty-hint {
      font-size: 11px;
    }
  }
}

// ==================== 标签页模式样式 ====================
.achievement-tab-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.tab-header {
  padding: 12px 16px;
  background: linear-gradient(90deg, rgba(255, 215, 0, 0.1), transparent);
  border-bottom: 1px solid rgba(255, 215, 0, 0.15);

  .header-stats-inline {
    display: flex;
    align-items: center;
    gap: 12px;

    .stat-badge {
      display: flex;
      align-items: center;
      gap: 6px;
      font-size: 12px;
      color: #a0b8d0;
      padding: 5px 12px;
      background: rgba(255, 255, 255, 0.05);
      border-radius: 14px;
      border: 1px solid rgba(255, 255, 255, 0.08);

      i {
        font-size: 11px;
        color: #ffd700;
      }

      &.title {
        background: rgba(255, 215, 0, 0.1);
        border-color: rgba(255, 215, 0, 0.25);
        color: #ffd700;
      }
    }
  }
}

.filter-tabs-wrapper.tab-mode {
  background: transparent;
  border-bottom: 1px solid var(--border-color, rgba(255, 255, 255, 0.08));
  
  .filter-tabs {
    background: transparent;
  }
}

.achievements-container.tab-mode {
  flex: 1;
  min-height: 180px;
  max-height: none;
}

.milestones-section.tab-mode {
  background: transparent;
  border-top: 1px solid var(--border-color, rgba(255, 255, 255, 0.08));
}

// 标签页模式下的手机端适配
@media screen and (max-width: 480px) {
  .tab-header {
    padding: 10px 12px;

    .header-stats-inline {
      gap: 8px;

      .stat-badge {
        font-size: 11px;
        padding: 4px 10px;
      }
    }
  }

  .achievements-container.tab-mode {
    min-height: 150px;
  }
}
